	module.exports = {"data":{"allPostTitles":{"edges":[{"node":{"frontmatter":{"title":"Configuration","doc":2,"section":1,"type":"doc"},"fields":{"slug":"/configuration"}}},{"node":{"frontmatter":{"title":"Docs","doc":4,"section":1,"type":"doc"},"fields":{"slug":"/docs"}}},{"node":{"frontmatter":{"title":"Getting Started","doc":1,"section":1,"type":"doc"},"fields":{"slug":"/getting-started"}}},{"node":{"frontmatter":{"title":"React","doc":3,"section":1,"type":"doc"},"fields":{"slug":"/react"}}}]},"postBySlug":{"html":"<p>Dazzling is the quickest and most flexible way to build websites for your projects. It requires minimal set up and preparations, and is backed by a powerful <a href=\"http://www.hygen.io\">generator framework</a>. </p>\n<p>Let's see what it takes to build your new website.</p>\n<p>You can use Dazzling in a new or existing project; as long as you have a target folder with a <code class=\"language-text\">package.json</code> in it, your all set.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ yarn add -D dazzling\n$ yarn dazzling site new</code></pre>\n      </div>\n<p>After running the <code class=\"language-text\">dazzling site</code> command it will prompt you with quick questions. You can keep the defaults or input your own answers - you can always edit later.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ dazzling site new\n? What&#39;s your project name? Bamboo\n? Project tagline? The scalable wood that bends and holds strong.\n? Github repo? https://github.com/fixme/fixme\n? Website? https://fixme.io\n? Google Analytics ID? UA-FIX-ME-XXXXX\n? Your name? (for copyright) Chuck Woody\n\nLoaded templates: dazzling/_templates\n       shell: cd &#39;dazzling/_templates/site/new/contents&#39; &amp;&amp; tar cf - --exclude=node_modules . | (cd daz-test &amp;&amp; tar xvf - )\n       added: daz-test/www/static/CNAME\n       added: daz-test/www/site-config.js</code></pre>\n      </div>\n<p>You're now ready to <code class=\"language-text\">cd www</code> and <code class=\"language-text\">yarn install</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">$ cd www\n$ yarn install\n$ yarn start</code></pre>\n      </div>\n<p>In a moment you'll have a new website ready, including documentation, with <a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby</a> as the backing engine.</p>\n<p>Next, let's see how to <a href=\"configuration\">configure your website</a>.</p>","timeToRead":1,"excerpt":"Dazzling is the quickest and most flexible way to build websites for your projects. It requires minimal set up and preparations, and is…","frontmatter":{"title":"Getting Started"},"fields":{"slug":"/getting-started"}}},"pathContext":{"slug":"/getting-started"}}

